import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/styles';
import { useSelector, useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import axios from 'axios';
import { yupResolver } from '@hookform/resolvers/yup';
import { FormProvider, RHFTextField, RHFSelect } from '../../../components/hook-form';
import {
    Button,
    Grid,
    Divider,
    IconButton,
    Paper,
    Checkbox,
    List,
    ListItem,
    ListItemText,
    ListSubheader,
    useTheme
} from '@material-ui/core';
import LibraryBooksIcon from '@mui/icons-material/LibraryBooks';
import validationSchema from '../utils/AddEditbookValidationSchema';
import useStyles from '../styles';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { useParams } from 'react-router-dom';
import MainCard from '../../../ui-component/cards/MainCard';
import { addBooks, clearGetByIdBooksData, getBooks, getByIdBooks, updateBooks } from '../../../store/actions/BookActions';
import { getAllCategory } from '../../../store/actions/BookCategoryActions';
import { getBookLanguageList } from '../../../store/actions/BookLanguageActions';
import useUserRole from '../../../hooks/useUserRole';
import { addBook, updateBook } from '../../../services/BookServices';
import { setMessage } from '../../../store/actions/MessageActions';
import { getActiveCategories } from '../../../services/activeCategories';

const useStyles1 = makeStyles((theme) => ({
    input: {
        '&:invalid': {
            border: 'red solid 2px'
        }
    }
}));

const STATUS_ITEMS = [
    { id: 0, status: 'ACTIVE' },
    { id: 1, status: 'INACTIVE' }
];

export default function AddBook() {
    const server = process.env.REACT_APP_ROOT_URL_BOOK;

    useUserRole({ roleList: ['ADMIN', 'SUPER_ADMIN'] });
    const classes = useStyles();
    const { id } = useParams();
    const history = useHistory();
    const dispatch = useDispatch();
    const { allSubCategories } = useSelector((state) => state?.category);
    const { bookByIdData, loading } = useSelector((state) => state?.books);
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);
    const { languagesList } = useSelector((state) => state?.language);
    const languageList = languagesList?.content;
    const [file, setFile] = useState(null);
    const [categoryId, setCategoryId] = useState([]);
    const [userCategory, setUserCategory] = useState([]);
    const [finalArray, setFinalArray] = useState([]);
    const [displayImage, setDisplayImage] = useState();
    const [selectedCity, setSelectedCity] = useState('');
    const [selectedCityId, setSelectedCityId] = useState('');

    // ======================================================
    const [activeCategoryData, setActiveCategoryData] = useState([]);

    const GetActiveCategories = async () => {
        await getActiveCategories()
            .then((res) => {
                setActiveCategoryData(res?.data);
            })
            .catch((error) => {
                console.log(error);
            });
    };
    useEffect(() => {
        GetActiveCategories();
    }, []);

    const initialValues = {
        title: '',
        description: '',
        categoryIds: '',
        paperType: '',
        author: '',
        publisher: '',
        isbn: '',
        totalQuantity: 1,
        availableQuantity: 1,
        languageId: '',
        series: 1,
        minAge: 1,
        maxAge: 15,
        shelfId: 0,
        // imageUrls: '',
        googleUrl: '',
        status: 'ACTIVE'
    };

    useEffect(() => {
        if (id) {
            setFinalArray(bookByIdData?.categoryIds || []);
            setCategoryId(bookByIdData?.categoryIds || []);
        }
    }, [id, bookByIdData]);

    useEffect(() => {
        if (id) {
            dispatch(getByIdBooks(id));
        }
    }, [id]);

    const handleChange = function loadFile(event) {
        setDisplayImage(URL.createObjectURL(event.target.files[0]));
        setFile(event.target.files[0]);
    };

    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(validationSchema),
        defaultValues: initialValues
    });
    const { reset, handleSubmit, setValue } = methods;

    useEffect(() => {
        if (bookByIdData && Object.keys(bookByIdData)?.length && id) {
            setValue('title', bookByIdData?.title);
            setValue('description', bookByIdData?.description);
            setValue('categoryIds', categoryId);
            setValue('paperType', bookByIdData?.paperType);
            setValue('author', bookByIdData?.author);
            setValue('publisher', bookByIdData?.publisher);
            setValue('isbn', bookByIdData?.isbn);
            setValue('totalQuantity', bookByIdData?.totalQuantity);
            setValue('languageId', bookByIdData?.languageId);
            setValue('series', bookByIdData?.series);
            setValue('minAge', bookByIdData?.minAge);
            setValue('maxAge', bookByIdData?.maxAge);
            setValue('shelfId', bookByIdData?.shelfId);
            setValue('googleUrl', bookByIdData?.googleUrl);
            setValue('status', bookByIdData?.status);
            setDisplayImage(bookByIdData?.imageUrls?.[0] ? bookByIdData?.imageUrls?.[0] : bookByIdData?.googleUrl);

            fetch(bookByIdData?.imageUrls?.[0], { mode: 'no-cors' }).then(async (response) => {
                const contentType = response.headers.get('content-type');
                const blob = await response.blob();
                const file = new File([blob], 'updatedImage.jpg', { contentType });
                //setFile(file);
            });
        }
    }, [bookByIdData, setValue]);

    const onSubmit = (data) => {
        const payload = {
            title: data?.title,
            paperType: data?.paperType,
            description: data?.description,
            author: data?.author,
            publisher: data?.publisher,
            isbn: data?.isbn,
            totalQuantity: data?.totalQuantity,
            availableQuantity: data?.availableQuantity,
            languageId: data?.languageId,
            series: data?.series,
            minAge: data?.minAge,
            maxAge: data?.maxAge,
            shelfId: data?.shelfId,
            // imageUrls: [data?.imageUrls],
            googleUrl: data?.googleUrl,
            categoryIds: categoryId,
            status: data?.status,
            cityId: selectedCityId
        };

        const formData = new FormData();

        if (file) {
            file && formData.append('images', file);
            if (id) formData.append('updateBookBody', JSON.stringify(payload));
            else formData.append('bookBody', JSON.stringify(payload));
        } else {
            if (id) formData.append('updateBookBody', JSON.stringify(payload));
            else formData.append('bookBody', JSON.stringify(payload));
        }

        if (id) {
            updateBook(formData, id)
                .then((data) => {
                    dispatch(setMessage(data?.message ? data?.message : 'Book  Updated Successfully'));
                })
                .catch((error) => {
                    console.log(error);
                    dispatch(setMessage(error?.data?.message ? error?.data?.message : 'Something Went Wrong'));
                });
            history.push('/admin/books/all-books');
        } else {
            addBook(formData)
                .then((data) => {
                    dispatch(setMessage(data?.message ? data?.message : 'Book  Added Successfully!'));
                })
                .catch((error) => {
                    dispatch(setMessage(error?.data?.message ? error?.data?.message : 'Something Went Wrong'));
                });
            history.push('/admin/books/all-books');
        }
    };
    const handleCheckboxChange = (event, id) => {
        let newArray = [...categoryId, event.target.id];
        if (categoryId.includes(event.target.id)) {
            newArray = newArray.filter((id) => id !== event.target.id);
        }
        if (finalArray.includes(id)) {
            let filterArr = finalArray?.filter((itm) => itm !== id);
            setFinalArray(filterArr);
        } else {
            setFinalArray([...finalArray, id]);
        }
        setCategoryId(newArray);
    };

    useEffect(() => {
        if (userCategory?.length > 0) {
            userCategory?.map((item) => {
                setFinalArray([...finalArray, item?.id]);
            });
        }
    }, [userCategory]);

    useEffect(() => {
        dispatch(getAllCategory());
        dispatch(getBookLanguageList());
    }, []);
    const [citiesList, setCitiesList] = useState([]);

    useEffect(() => {
        // Fetch cities data using Axios
        axios
            .get(`${server}/books-service/books/getAllCities`)
            .then((res) => {
                console.log('This is Response', res.data);
                const cities = res?.data || [];

                const cityObjects = cities.map((city) => ({ id: city.id, name: city.name }));
                console.log('Ye City Objects h', cityObjects);
                setCitiesList(cityObjects);
            })
            .catch((error) => {
                console.log(error);
            });
    }, []);

    console.log('Cities List Yha Ari h', citiesList);
    console.log('Selected Cities List mese id Ari h', selectedCityId);
    citiesList.map((city) => console.log('These are individual cities', city.id, city.name));

    useEffect(() => {
        console.log('Selected City ka nam:', selectedCity);
        let selectedCityId = '';

        for (let i = 0; i < citiesList.length; i++) {
            if (citiesList[i].name === selectedCity) {
                selectedCityId = citiesList[i].id;
                break;
            }
        }

        setSelectedCityId(selectedCityId);
    }, [selectedCity]);

    return (
        <>
            <MainCard>
                <div className={classes.titleContainer}>
                    <LibraryBooksIcon className={classes.viewIcon} />
                    <span className={classes.pageTitle}>&nbsp; {id ? 'Update' : 'Add'} Book </span>
                </div>
                <Paper elevation={3} className={classes.paper}>
                    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                        <Grid container spacing={4}>
                            <Grid item container xs={12} sm={6}>
                                <Grid item xs={12} sm={12}>
                                    <label className={classes.label}>
                                        Book Category <span className={classes.mandatory}> * </span>
                                    </label>
                                    <PerfectScrollbar className={classes.ScrollHeight}>
                                        <List subheader={<li />}>
                                            {activeCategoryData?.map((parent) => (
                                                <li key={parent?.id} className={classes.listSection}>
                                                    <ListSubheader style={{ fontSize: 16, fontWeight: 'lighter' }}>
                                                        {parent?.title}
                                                    </ListSubheader>

                                                    {parent?.categories?.map((child) => (
                                                        <ListItem>
                                                            <Checkbox
                                                                onChange={(e) => handleCheckboxChange(e, child?.id)}
                                                                type="checkbox"
                                                                id={child?.id}
                                                                checked={finalArray.includes(child?.id)}
                                                            />

                                                            <ListItemText primary={child?.title} />
                                                        </ListItem>
                                                    ))}
                                                </li>
                                            ))}
                                        </List>
                                    </PerfectScrollbar>
                                </Grid>
                            </Grid>
                            <Grid item xs={12} sm={6} className={classes.imageContainer}>
                                <label className={classes.label}>
                                    Book Image <span className={classes.mandatory}> * </span>
                                </label>
                                <Grid item xs={12}>
                                    {!id ? (
                                        <input
                                            type="file"
                                            onChange={handleChange}
                                            id="upload"
                                            accept="image/*"
                                            className={classes.imageInput}
                                            required
                                        />
                                    ) : (
                                        <input
                                            type="file"
                                            onChange={handleChange}
                                            id="upload"
                                            accept="image/*"
                                            className={classes.imageInput}
                                        />
                                    )}

                                    <label htmlFor="upload">
                                        <IconButton
                                            color="secondary"
                                            aria-label="upload picture"
                                            component="span"
                                            style={{ height: 170, width: 150 }}
                                        >
                                            <img id="ImageCover" src={displayImage} className={classes.img} />
                                        </IconButton>
                                    </label>
                                    <label htmlFor="bookCover" />
                                </Grid>
                            </Grid>
                        </Grid>
                        <Divider style={{ marginTop: 50, marginBottom: 0 }} />
                        <Grid container spacing={3}>
                            <Grid item xs={12} sm={6}>
                                <label className={classes.label}>
                                    Book Title <span className={classes.mandatory}>*</span>
                                </label>
                                {/* <RHFTextField name="title" required inputProps={{ className: classes1.input, pattern: '[a-z]{1,15}' }} /> */}
                                <RHFTextField name="title" required />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <label className={classes.label}>
                                    Book Paper Type <span className={classes.mandatory}> * </span>
                                </label>
                                <RHFTextField name="paperType" required />
                            </Grid>
                            <Grid item xs={12} sm={12}>
                                <label className={classes.label}>
                                    Book Description <span className={classes.mandatory}>*</span>
                                </label>
                                <RHFTextField name="description" required />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <label className={classes.label}>
                                    Book Author <span className={classes.mandatory}> * </span>
                                </label>
                                <RHFTextField name="author" required />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <label className={classes.label}>
                                    Book Publisher <span className={classes.mandatory}> * </span>
                                </label>
                                <RHFTextField name="publisher" required />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <label className={classes.label}>
                                    Book ISBN <span className={classes.mandatory}> * </span>
                                </label>
                                <RHFTextField name="isbn" required />
                            </Grid>
                            <Grid item xs={12} sm={6} style={{ display: id ? 'none' : 'block' }}>
                                {/* <Grid item xs={12} sm={6}> */}
                                <label className={classes.label}>
                                    Total Quantity <span className={classes.mandatory}> * </span>
                                </label>
                                {/* <RHFTextField name="totalQuantity" required type="number" /> */}
                                <RHFTextField name="totalQuantity" type="number" />
                            </Grid>
                            <Grid item xs={12} sm={6} style={{ display: id ? 'none' : 'block' }}>
                                {/* <Grid item xs={12} sm={6}> */}
                                <label className={classes.label}>
                                    Available Quantity <span className={classes.mandatory}> * </span>
                                </label>
                                <RHFTextField name="availableQuantity" type="number" />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <label className={classes.label}>
                                    Book Language <span className={classes.mandatory}> * </span>
                                </label>
                                <RHFSelect name="languageId" label="" required>
                                    {!id && <option value=""> Select Book Language </option>}
                                    {languageList?.map((item, index) => (
                                        <option key={item?.id} value={item?.id}>
                                            {item?.name}
                                        </option>
                                    ))}
                                </RHFSelect>
                            </Grid>

                            <Grid item xs={12} sm={6}>
                                <label className={classes.label}> Book Series </label>
                                <RHFTextField name="series" type="number" />
                            </Grid>

                            <Grid item xs={12} sm={6}>
                                <label className={classes.label}> Min Age </label>
                                <RHFTextField name="minAge" type="number" />
                            </Grid>

                            <Grid item xs={12} sm={6}>
                                <label className={classes.label}> Max Age </label>
                                <RHFTextField name="maxAge" type="number" />
                            </Grid>

                            <Grid item xs={12} sm={6}>
                                <label className={classes.label}> Shelf id </label>
                                <RHFTextField name="shelfId" />
                            </Grid>

                            <Grid item xs={12} sm={6}>
                                <label className={classes.label}> Google Url </label>
                                <RHFTextField name="googleUrl" />
                            </Grid>

                            <Grid item xs={12} sm={6}>
                                <label className={classes.label}>
                                    Status<span className={classes.mandatory}>*</span>
                                </label>
                                <RHFSelect name="status" label="" required>
                                    <option value=""> Select Status </option>
                                    {STATUS_ITEMS?.map((item, index) => (
                                        <option key={index} value={item?.status}>
                                            {item?.status}
                                        </option>
                                    ))}
                                </RHFSelect>
                            </Grid>

                            {/* {id ? 'Update' : 'Add'} */}
                            {/* {console.log('id---->', id)} */}

                            {id === '' || id === undefined ? (
                                <Grid item xs={12} sm={6}>
                                    <label className={classes.label}>
                                        City<span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFSelect name="cityId" label="" required onChange={(e) => setSelectedCity(e.target.value)}>
                                        <option value=""> Select City </option>
                                        {citiesList.length > 0 ? (
                                            citiesList.map((city) => (
                                                <option key={city.id} value={city.name}>
                                                    {city.name}
                                                </option>
                                            ))
                                        ) : (
                                            <option value="" disabled>
                                                No cities fetched
                                            </option>
                                        )}
                                    </RHFSelect>
                                </Grid>
                            ) : (
                                <></>
                            )}
                        </Grid>

                        <Button variant="contained" color="primary" type="submit" className={classes.submit}>
                            {id ? 'Update' : 'Add'}
                        </Button>
                    </FormProvider>
                </Paper>
            </MainCard>
        </>
    );
}
