import { http } from '../utils/http';

export const addBook = (payload) => {
    return new Promise((resolve, reject) => {
        http(
            process.env.REACT_APP_ROOT_URL_BOOK,
            'POST',
            `books-service/books`,
            payload,
            true,
            { 'content-type': 'multipart/form-data', accept: '*/*' },
            {}
        )
            .then((data) => {
                resolve(data);
            })
            .catch((err) => {
                reject(err);
            });
    });
};

export const updateBook = (payload, bookId) => {
    return new Promise((resolve, reject) => {
        http(
            process.env.REACT_APP_ROOT_URL_BOOK,
            'PUT',
            `books-service/books/${bookId}`,
            payload,
            true,
            { 'content-type': 'multipart/form-data', accept: '*/*' },
            {}
        )
            .then((data) => {
                resolve(data);
            })
            .catch((err) => {
                reject(err);
            });
    });
};
