import { http } from '../../../utils/http';
import { setMessage } from '../MessageActions';
import { GET_ALL_LANGUAGE_START, GET_ALL_LANGUAGE_SUCCESS, GET_ALL_LANGUAGE_ERROR } from '../../constants';

const getAllBookLanguageStart = () => {
    return { type: GET_ALL_LANGUAGE_START };
};
const getAllBookLanguageSuccess = (payload) => {
    return { type: GET_ALL_LANGUAGE_SUCCESS, payload };
};
const getAllBookLanguageError = (error) => {
    return { type: GET_ALL_LANGUAGE_ERROR, error };
};

const addBookLanguage = (payload, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_BOOK, 'POST', `books-service/languages`, payload, true)
            .then((res) => {
                dispatch(setMessage(res?.message));
                successCB && successCB(res);
            })
            .catch((err) => {
                console.log(err);
                dispatch(setMessage(err?.data?.message ? err?.data?.message : 'Error!'));
            });
    } catch (error) {
        console.log(error);
    }
};

const updateBookLanguage = (payload, id, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_BOOK, 'PUT', `books-service/languages/${id}`, payload, true)
            .then((res) => {
                if (res) {
                    dispatch(setMessage(res?.message));
                    successCB && successCB(res);
                }
            })
            .catch((err) => {
                console.log(err);
                dispatch(setMessage(err?.data?.message ? err?.data?.message : 'Error!'));
            });
    } catch (error) {
        console.log(error);
    }
};

const getBookLanguageList = () => (dispatch, getState) => {
    dispatch(getAllBookLanguageStart());
    try {
        http(process.env.REACT_APP_ROOT_URL_BOOK, 'GET', `books-service/languages`, true)
            .then((res) => {
                const payload = {
                    languagesList: res
                };
                dispatch(getAllBookLanguageSuccess(payload));
            })
            .catch((err) => {
                dispatch(getAllBookLanguageError(err));
                dispatch(setMessage(err?.message));
            });
    } catch (error) {
        dispatch(getAllBookLanguageError('Something went wrong'));
    }
};

const getAllBookLanguage =
    (rowsPerPage = 10, searchData = '', page = 1) =>
    (dispatch) => {
        dispatch(getAllBookLanguageStart());
        try {
            http(
                process.env.REACT_APP_ROOT_URL_BOOK,
                'GET',
                `books-service/languages?searchKey=${searchData}&pageNumber=${page}&pageSize=${rowsPerPage}`,
                true
            )
                .then((res) => {
                    const payload = {
                        allLanguages: res
                    };
                    dispatch(getAllBookLanguageSuccess(payload));
                })
                .catch((err) => {
                    dispatch(getAllBookLanguageError(err));
                    dispatch(setMessage(err?.message));
                });
        } catch (error) {
            dispatch(getAllBookLanguageError('Something went wrong'));
        }
    };

export { updateBookLanguage, getAllBookLanguage, addBookLanguage, getBookLanguageList };
