import * as yup from 'yup';

const validationSchema = yup.object({
    title: yup.string('Enter book title').required('Book title is required').trim(),
    description: yup.string('Enter book details').required('Book details is required').trim(),
    paperType: yup.string('Enter book paper type').required('Paper type is required').trim(),
    author: yup.string('Enter book auther').required('Book auther is required').trim(),
    publisher: yup.string('Enter book publisher').required('Book publisher is required').trim(),
    isbn: yup.string('Enter book ISBN').required('Book ISBN is required').trim(),

    minAge: yup.number().min(1).max(15),
    maxAge: yup.number().nullable().moreThan(yup.ref('minAge')).max(15),

    // totalQuantity: yup.number('Enter no. book copy').typeError('Quantity must be a number'),
    // availableQuantity: yup.string('Enter no. of book quantity').required('Book quantity is required'),
    languageId: yup.string('Select book language').required('Book language is required'),
    // series: yup.string('Enter book series').required('Book series is required'),
    status: yup.string('Enter book status').required('Book status is required')
});

export default validationSchema;
