import { http } from '../utils/http';
import axios from 'axios';

export const getActiveCategories = () => {
    return new Promise((resolve, reject) => {
        axios
            .get(`${process.env.REACT_APP_ROOT_URL_BOOK}/books-service/parent-categories/categories`)
            .then((data) => {
                resolve(data);
            })
            .catch((err) => {
                reject(err);
            });
    });
};
